import { sendMailWithPopover } from '@engine/utils';
import { sendEmail } from '../hyper_link.crud';
import { AbstractLink, HyperLinkProps } from './abstract.link';

export class MailLink extends AbstractLink {
    protected mailLinkProps: MailLinkProps;
    public constructor(props: MailLinkProps) {
        super(props);
        this.mailLinkProps = props;
    }

    protected _excute() {
        sendMailWithPopover(this.mailLinkProps, sendEmail);
    }
}

/**
 * title 标题
 * cc 抄送
 * cc 密送
 * mailTo 收件人
 * mailText 正文
 * showTplContent 正文预览报表内容
 */
export interface MailLinkProps extends HyperLinkProps {
    title: string;
    cc: string;
    bcc: string;
    mailTo: string;
    mailText: string;
    showTplContent: boolean;
    parameters: string;
}
